	module.exports = {"data":{"post":{"html":"<p><a href=\"http://www.flickr.com/photos/51035717986@N01/1419401356\" title=\"View 'CIMG0158.JPG' on Flickr.com\"><img src=\"https://farm2.static.flickr.com/1411/1419401356_11c10796dd.jpg\" alt=\"CIMG0158.JPG\" border=\"0\" width=\"375\" height=\"500\" /></a></p>\n<p>We're home from our meetings, which was indeed on board the AIDAaura cruise ship which was chartered for the occasion. We sailed a circle from Palma de Mallorca to Cannes and Barcelona, then back to Palma.</p>\n<p>For our shore leaves we chose to take mountain bike tours. Here I am yesterday in front of the <a href=\"http://en.wikipedia.org/wiki/Palau_Nacional\">Palau Nacional</a> in Barcelona. I'm smiling only because I don't realize that we're not even half way to the Montju&iuml;c castle. I later learned that sane people take a cable car to the castle rather than a mountain bike.</p>","fields":{"title":"Bike Tour","date":"21 Sep 2007","url":"https://papascott.de/2007-09-21-bike-tour/"}}},"pathContext":{"slug":"/2007-09-21-bike-tour/","prev":{"fields":{"title":"Radio Silence","date":"16 Sep 2007","slug":"/2007-09-16-radio-silence/"}},"next":{"fields":{"title":"Adieu DB","date":"29 Sep 2007","slug":"/2007-09-29-adieu-db/"}}}}

